<template>
  <div class="spliter">
    <h5>字符分隔</h5>
    <div class="content">
      <div class="form">
        <div class="item">
          <div class="label">输入字符</div>
          <q-input
            class="field"
            v-model="input"
            type="textarea"
            rows="7"
            color="faded"
            placeholder="输入需要分割的字符串"
          />
        </div>
        <div class="item">
          <div class="label">长度</div>
          <q-input class="field" type="number" v-model="fragment" color="primary" />
        </div>
        <div class="item">
          <div class="label">自动转大写</div>
          <q-toggle class="field" v-model="capital" color="primary" />
        </div>
        <div class="item">
          <div class="label">分隔符</div>
          <q-select
            class="field"
            v-model="separator"
            :options="separatorOptions"
          />
        </div>
        <div class="item" v-if="separator === 'custom'">
          <div class="label">自定义分隔符</div>
          <q-input placeholder="输入自定义分隔符" class="field" v-model="customSeparator" color="primary" />
        </div>
        <div class="item" >
          <div class="label">&nbsp;</div>
          <q-btn @click="saveConfig">保存当前配置</q-btn>
        </div>
      </div>
      <div class="result">
        <div class="desc">分隔输出</div>
        <div class="display">
          <div class="output" v-if="input" v-html="output"></div>
          <span class="empty" v-else>还没有字符输入</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const saveKeys = ['capital', 'separator', 'customSeparator', 'fragment']

export default {
  data() {
    return {
      input: '',
      capital: true,
      separator: '\n',
      customSeparator: '',
      fragment: 10,
      separatorOptions: [
        {value: '\n', label: '换行'},
        {value: ', ', label: '逗号'},
        {value: '. ', label: '点'},
        {value: '-', label: '划线'},
        {value: 'custom', label: '自定义'},
      ]
    }
  },

  mounted() {
    const ls = localStorage.getItem('TOOL_CONFIG')
    if (!ls) {
      return
    }
    try {
      const config = JSON.parse(ls)
      saveKeys.forEach(key => {
        if (key in config) {
          this[key] = config[key]
        }
      })
    } catch (e) {
      // pass
    }
  },

  computed: {
    output() {
      const input = this.capital ? this.input.trim().toUpperCase() : this.input.trim()
      const parts = Math.ceil(input.length / this.fragment)
      const separator = this.separator === 'custom' ? this.customSeparator : this.separator
      return Array(parts).fill(true).map((e, index) => {
        const start = this.fragment * index
        return input.slice(start, start + this.fragment)
      }).join(separator).replace(/\n/g, '<br/>')
    }
  },

  methods: {
    saveConfig() {
      const obj = {}
      saveKeys.forEach(key => {
        obj[key] = this[key]
      })
      localStorage.setItem('TOOL_CONFIG', JSON.stringify(obj))
    }
  }
}
</script>

